import React from "react";
import CardListing from "../../components/CardList";
import { Box, Anchor } from "grommet";

const Home = ({ stores, publicPage }) => {
  const displayAnchor = (e, isPublic) => isPublic ? <Anchor href={`/store/${e.id}`}>Go to Store</Anchor> :
  <><Anchor href={`/account/store/editstore/${e.id}`}>Edit</Anchor>
  <Anchor href={`/account/store/manageproduct/${e.id}`}>Manage Product</Anchor></>
  return (
    <Box align="center" alignContent="center" justify="center">
      <CardListing
        dataList={stores.map((e) => ({
          title: e.get("name"),
          cta: displayAnchor(e, publicPage)
        }))}
      />
    </Box>
  );
};

export default Home;
